module.exports = "import { Component, OnInit } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-table-fixed-header',\r\n  template: `\r\n    <nz-table #nzTable [nzData]=\"dataSet\" [nzPageSize]=\"50\" [nzScroll]=\"{ y: '240px' }\">\r\n      <thead>\r\n        <tr>\r\n          <th nzWidth=\"150px\">Name</th>\r\n          <th nzWidth=\"150px\">Age</th>\r\n          <th>Address</th>\r\n        </tr>\r\n      </thead>\r\n      <tbody>\r\n        <tr *ngFor=\"let data of nzTable.data\">\r\n          <td>{{data.name}}</td>\r\n          <td>{{data.age}}</td>\r\n          <td>{{data.address}}</td>\r\n        </tr>\r\n      </tbody>\r\n    </nz-table>`,\r\n  styles  : []\r\n})\r\nexport class NzDemoTableFixedHeaderComponent implements OnInit {\r\n  dataSet = [];\r\n\r\n  ngOnInit(): void {\r\n    for (let i = 0; i < 100; i++) {\r\n      this.dataSet.push({\r\n        name   : `Edward King ${i}`,\r\n        age    : 32,\r\n        address: `London, Park Lane no. ${i}`\r\n      });\r\n    }\r\n  }\r\n}"