module.exports = "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-table-fixed-columns',\r\n  template: `\r\n    <nz-table #nzTable [nzData]=\"dataSet\" [nzPageSize]=\"10\" [nzScroll]=\"{x:'1300px'}\">\r\n      <thead>\r\n        <tr>\r\n          <th nzWidth=\"100px\" nzLeft=\"0px\">Full Name</th>\r\n          <th nzWidth=\"100px\" nzLeft=\"100px\">Age</th>\r\n          <th>Column 1</th>\r\n          <th>Column 2</th>\r\n          <th>Column 3</th>\r\n          <th>Column 4</th>\r\n          <th>Column 5</th>\r\n          <th>Column 6</th>\r\n          <th>Column 7</th>\r\n          <th>Column 8</th>\r\n          <th nzWidth=\"100px\" nzRight=\"0px\">Action</th>\r\n        </tr>\r\n      </thead>\r\n      <tbody>\r\n        <tr *ngFor=\"let data of nzTable.data\">\r\n          <td nzLeft=\"0px\">{{data.name}}</td>\r\n          <td nzLeft=\"100px\">{{data.age}}</td>\r\n          <td>{{data.address}}</td>\r\n          <td>{{data.address}}</td>\r\n          <td>{{data.address}}</td>\r\n          <td>{{data.address}}</td>\r\n          <td>{{data.address}}</td>\r\n          <td>{{data.address}}</td>\r\n          <td>{{data.address}}</td>\r\n          <td>{{data.address}}</td>\r\n          <td nzRight=\"0px\">\r\n            <a>action</a>\r\n          </td>\r\n        </tr>\r\n      </tbody>\r\n    </nz-table>`,\r\n  styles  : []\r\n})\r\nexport class NzDemoTableFixedColumnsComponent {\r\n  dataSet = [\r\n    {\r\n      key    : '1',\r\n      name   : 'John Brown',\r\n      age    : 32,\r\n      address: 'New York Park',\r\n    },\r\n    {\r\n      key    : '2',\r\n      name   : 'Jim Green',\r\n      age    : 40,\r\n      address: 'London Park',\r\n    }\r\n  ];\r\n}\r\n"