module.exports = "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-table-colspan-rowspan',\r\n  template: `\r\n    <nz-table #nzTable [nzData]=\"dataSet\" [nzPageSize]=\"10\" nzBordered>\r\n      <thead>\r\n        <tr>\r\n          <th>Name</th>\r\n          <th>Age</th>\r\n          <th colspan=\"2\">Home phone</th>\r\n          <th>Address</th>\r\n        </tr>\r\n      </thead>\r\n      <tbody>\r\n        <tr *ngFor=\"let data of nzTable.data; index as i;\">\r\n          <td>{{data.name}}</td>\r\n          <td [attr.colspan]=\"i==4?5:1\">{{data.age}}</td>\r\n          <td [attr.rowspan]=\"i==2?2:1\" *ngIf=\"(i!=3)&&(i!=4)\">{{data.tel}}</td>\r\n          <td *ngIf=\"i!=4\">{{data.phone}}</td>\r\n          <td *ngIf=\"i!=4\">{{data.address}}</td>\r\n        </tr>\r\n      </tbody>\r\n    </nz-table>`,\r\n  styles  : []\r\n})\r\nexport class NzDemoTableColspanRowspanComponent {\r\n  dataSet = [\r\n    {\r\n      key    : '1',\r\n      name   : 'John Brown',\r\n      age    : 32,\r\n      tel    : '0571-22098909',\r\n      phone  : 18889898989,\r\n      address: 'New York No. 1 Lake Park',\r\n    },\r\n    {\r\n      key    : '2',\r\n      name   : 'Jim Green',\r\n      tel    : '0571-22098333',\r\n      phone  : 18889898888,\r\n      age    : 42,\r\n      address: 'London No. 1 Lake Park',\r\n    },\r\n    {\r\n      key    : '3',\r\n      name   : 'Joe Black',\r\n      age    : 32,\r\n      tel    : '0575-22098909',\r\n      phone  : 18900010002,\r\n      address: 'Sidney No. 1 Lake Park',\r\n    },\r\n    {\r\n      key    : '4',\r\n      name   : 'Jim Red',\r\n      age    : 18,\r\n      tel    : '0575-22098909',\r\n      phone  : 18900010002,\r\n      address: 'London No. 2 Lake Park',\r\n    },\r\n    {\r\n      key    : '5',\r\n      name   : 'Jake White',\r\n      age    : 18,\r\n      tel    : '0575-22098909',\r\n      phone  : 18900010002,\r\n      address: 'Dublin No. 2 Lake Park',\r\n    }\r\n  ];\r\n}\r\n"