module.exports = "import { Component, OnInit } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-table-edit-cell',\r\n  template: `\r\n    <button nz-button (click)=\"addRow()\" class=\"editable-add-btn\">Add</button>\r\n    <nz-table\r\n      #editRowTable\r\n      nzBordered\r\n      [nzData]=\"dataSet\">\r\n      <thead>\r\n        <tr>\r\n          <th nzWidth=\"30%\">Name</th>\r\n          <th>Age</th>\r\n          <th>Address</th>\r\n          <th>Action</th>\r\n        </tr>\r\n      </thead>\r\n      <tbody>\r\n        <tr *ngFor=\"let data of editRowTable.data\">\r\n          <td>\r\n            <div class=\"editable-cell\">\r\n              <div class=\"editable-cell-text-wrapper\">\r\n                <ng-container *ngIf=\"!editCache[data.key].edit\">\r\n                  {{data.name}}\r\n                  <i class=\"anticon anticon-edit editable-cell-icon\" (click)=\"startEdit(data.key)\"></i>\r\n                </ng-container>\r\n                <ng-container *ngIf=\"editCache[data.key].edit\">\r\n                  <input type=\"text\" nz-input [(ngModel)]=\"editCache[data.key].name\">\r\n                  <i class=\"anticon anticon-check editable-cell-icon-check\" (click)=\"finishEdit(data.key)\"></i>\r\n                </ng-container>\r\n              </div>\r\n            </div>\r\n          </td>\r\n          <td>{{data.age}}</td>\r\n          <td>{{data.address}}</td>\r\n          <td>\r\n            <nz-popconfirm [nzTitle]=\"'Sure to delete?'\" (nzOnConfirm)=\"deleteRow(data.key)\">\r\n              <a nz-popconfirm>Delete</a>\r\n            </nz-popconfirm>\r\n          </td>\r\n        </tr>\r\n      </tbody>\r\n    </nz-table>\r\n  `,\r\n  styles  : [\r\n      `\r\n      .editable-cell {\r\n        position: relative;\r\n      }\r\n\r\n      .editable-cell-input-wrapper,\r\n      .editable-cell-text-wrapper {\r\n        padding-right: 24px;\r\n      }\r\n\r\n      .editable-cell-text-wrapper {\r\n        padding: 5px 24px 5px 5px;\r\n      }\r\n\r\n      .editable-cell-icon,\r\n      .editable-cell-icon-check {\r\n        position: absolute;\r\n        right: 0;\r\n        width: 20px;\r\n        cursor: pointer;\r\n      }\r\n\r\n      .editable-cell-icon {\r\n        line-height: 18px;\r\n        display: none;\r\n      }\r\n\r\n      .editable-cell-icon-check {\r\n        line-height: 28px;\r\n      }\r\n\r\n      .editable-cell:hover .editable-cell-icon {\r\n        display: inline-block;\r\n      }\r\n\r\n      .editable-cell-icon:hover,\r\n      .editable-cell-icon-check:hover {\r\n        color: #108ee9;\r\n      }\r\n\r\n      .editable-add-btn {\r\n        margin-bottom: 8px;\r\n      }\r\n    `\r\n  ]\r\n})\r\nexport class NzDemoTableEditCellComponent implements OnInit {\r\n  i = 1;\r\n  editCache = {};\r\n  dataSet = [\r\n    {\r\n      key    : '0',\r\n      name   : 'Edward King 0',\r\n      age    : '32',\r\n      address: 'London, Park Lane no. 0'\r\n    },\r\n    {\r\n      key    : '1',\r\n      name   : 'Edward King 1',\r\n      age    : '32',\r\n      address: 'London, Park Lane no. 1'\r\n    }\r\n  ];\r\n\r\n  addRow(): void {\r\n    this.i++;\r\n    this.dataSet = [ ...this.dataSet, {\r\n      key    : `${this.i}`,\r\n      name   : `Edward King ${this.i}`,\r\n      age    : '32',\r\n      address: `London, Park Lane no. ${this.i}`\r\n    } ];\r\n    this.updateEditCache();\r\n  }\r\n\r\n  deleteRow(i: string): void {\r\n    const dataSet = this.dataSet.filter(d => d.key !== i);\r\n    this.dataSet = dataSet;\r\n  }\r\n\r\n  startEdit(key: string): void {\r\n    this.editCache[ key ].edit = true;\r\n  }\r\n\r\n  finishEdit(key: string): void {\r\n    this.editCache[ key ].edit = false;\r\n    this.dataSet.find(item => item.key === key).name = this.editCache[ key ].name;\r\n  }\r\n\r\n  updateEditCache(): void {\r\n    this.dataSet.forEach(item => {\r\n      if (!this.editCache[ item.key ]) {\r\n        this.editCache[ item.key ] = {\r\n          edit: false,\r\n          name: item.name\r\n        };\r\n      }\r\n    });\r\n  }\r\n\r\n  ngOnInit(): void {\r\n    this.updateEditCache();\r\n  }\r\n}\r\n"