module.exports = "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-table-custom-filter-panel',\r\n  template: `\r\n    <nz-table #nzTable [nzData]=\"displayData\">\r\n      <thead>\r\n        <tr>\r\n          <th>\r\n            Name\r\n            <nz-dropdown nzTrigger=\"click\" [nzClickHide]=\"false\">\r\n              <i class=\"anticon anticon-smile-o ant-table-filter-icon\" nz-dropdown></i>\r\n              <div class=\"custom-filter-dropdown\">\r\n                <input type=\"text\" nz-input placeholder=\"Search name\" [(ngModel)]=\"searchValue\">\r\n                <button nz-button [nzType]=\"'primary'\" (click)=\"search()\">Search</button>\r\n              </div>\r\n            </nz-dropdown>\r\n          </th>\r\n          <th>Age</th>\r\n          <th nzShowFilter [nzFilters]=\"filterAddressArray\" (nzFilterChange)=\"filterAddressChange($event)\">Address</th>\r\n        </tr>\r\n      </thead>\r\n      <tbody>\r\n        <tr *ngFor=\"let data of nzTable.data\">\r\n          <td>{{data.name}}</td>\r\n          <td>{{data.age}}</td>\r\n          <td>{{data.address}}</td>\r\n        </tr>\r\n      </tbody>\r\n    </nz-table>`,\r\n  styles  : [\r\n      `\r\n      .custom-filter-dropdown {\r\n        padding: 8px;\r\n        border-radius: 6px;\r\n        background: #fff;\r\n        box-shadow: 0 1px 6px rgba(0, 0, 0, .2);\r\n      }\r\n\r\n      [nz-input] {\r\n        width: 130px;\r\n        margin-right: 8px;\r\n      }\r\n\r\n      .highlight {\r\n        color: #f50;\r\n      }\r\n    `\r\n  ]\r\n})\r\nexport class NzDemoTableCustomFilterPanelComponent {\r\n  searchValue = '';\r\n  filterAddressArray = [\r\n    { text: 'London', value: 'London' },\r\n    { text: 'Sidney', value: 'Sidney' }\r\n  ];\r\n  searchAddress = [];\r\n  sortMap = {\r\n    name   : null,\r\n    age    : null,\r\n    address: null\r\n  };\r\n  sortName = null;\r\n  sortValue = null;\r\n  data = [\r\n    {\r\n      name   : 'John Brown',\r\n      age    : 32,\r\n      address: 'New York No. 1 Lake Park'\r\n    },\r\n    {\r\n      name   : 'Jim Green',\r\n      age    : 42,\r\n      address: 'London No. 1 Lake Park'\r\n    },\r\n    {\r\n      name   : 'Joe Black',\r\n      age    : 32,\r\n      address: 'Sidney No. 1 Lake Park'\r\n    },\r\n    {\r\n      name   : 'Jim Red',\r\n      age    : 32,\r\n      address: 'London No. 2 Lake Park'\r\n    }\r\n  ];\r\n  displayData = [ ...this.data ];\r\n\r\n  sort(sortName: string, value: boolean): void {\r\n    this.sortName = sortName;\r\n    this.sortValue = value;\r\n    for (const key in this.sortMap) {\r\n      this.sortMap[ key ] = (key === sortName ? value : null);\r\n    }\r\n    this.search();\r\n  }\r\n\r\n  filterAddressChange(value: string[]): void {\r\n    this.searchAddress = value;\r\n    this.search();\r\n  }\r\n\r\n  search(): void {\r\n    const filterFunc = (item) => {\r\n      return (this.searchAddress.length ? this.searchAddress.some(address => item.address.indexOf(address) !== -1) : true) &&\r\n        (item.name.indexOf(this.searchValue) !== -1);\r\n    };\r\n    const data = this.data.filter(item => filterFunc(item));\r\n    this.displayData = data.sort((a, b) => (this.sortValue === 'ascend') ? (a[ this.sortName ] > b[ this.sortName ] ? 1 : -1) : (b[ this.sortName ] > a[ this.sortName ] ? 1 : -1));\r\n  }\r\n}\r\n"