module.exports = "import { Component, OnInit } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-table-dynamic-settings',\r\n  template: `\r\n    <div class=\"components-table-demo-control-bar\">\r\n      <form nz-form nzLayout=\"inline\">\r\n        <nz-form-item>\r\n          <nz-form-label>\r\n            <label>Bordered</label>\r\n          </nz-form-label>\r\n          <nz-form-control>\r\n            <nz-switch [(ngModel)]=\"bordered\" name=\"bordered\"></nz-switch>\r\n          </nz-form-control>\r\n        </nz-form-item>\r\n        <nz-form-item>\r\n          <nz-form-label>\r\n            <label>Loading</label>\r\n          </nz-form-label>\r\n          <nz-form-control>\r\n            <nz-switch [(ngModel)]=\"loading\" name=\"loading\"></nz-switch>\r\n          </nz-form-control>\r\n        </nz-form-item>\r\n        <nz-form-item>\r\n          <nz-form-label>\r\n            <label>Pagination</label>\r\n          </nz-form-label>\r\n          <nz-form-control>\r\n            <nz-switch [(ngModel)]=\"pagination\" name=\"pagination\"></nz-switch>\r\n          </nz-form-control>\r\n        </nz-form-item>\r\n        <nz-form-item>\r\n          <nz-form-label>\r\n            <label>Title</label>\r\n          </nz-form-label>\r\n          <nz-form-control>\r\n            <nz-switch [(ngModel)]=\"title\" name=\"title\"></nz-switch>\r\n          </nz-form-control>\r\n        </nz-form-item>\r\n        <nz-form-item>\r\n          <nz-form-label>\r\n            <label>Column Header</label>\r\n          </nz-form-label>\r\n          <nz-form-control>\r\n            <nz-switch [(ngModel)]=\"header\" name=\"header\"></nz-switch>\r\n          </nz-form-control>\r\n        </nz-form-item>\r\n        <nz-form-item>\r\n          <nz-form-label>\r\n            <label>Footer</label>\r\n          </nz-form-label>\r\n          <nz-form-control>\r\n            <nz-switch [(ngModel)]=\"footer\" name=\"footer\"></nz-switch>\r\n          </nz-form-control>\r\n        </nz-form-item>\r\n        <nz-form-item>\r\n          <nz-form-label>\r\n            <label>Expandable</label>\r\n          </nz-form-label>\r\n          <nz-form-control>\r\n            <nz-switch [(ngModel)]=\"expandable\" name=\"expandable\"></nz-switch>\r\n          </nz-form-control>\r\n        </nz-form-item>\r\n        <nz-form-item>\r\n          <nz-form-label>\r\n            <label>Checkbox</label>\r\n          </nz-form-label>\r\n          <nz-form-control>\r\n            <nz-switch [(ngModel)]=\"checkbox\" name=\"checkbox\"></nz-switch>\r\n          </nz-form-control>\r\n        </nz-form-item>\r\n        <nz-form-item>\r\n          <nz-form-label>\r\n            <label>Fixed Header</label>\r\n          </nz-form-label>\r\n          <nz-form-control>\r\n            <nz-switch [(ngModel)]=\"fixHeader\" name=\"fixHeader\"></nz-switch>\r\n          </nz-form-control>\r\n        </nz-form-item>\r\n        <nz-form-item>\r\n          <nz-form-label>\r\n            <label>No Result</label>\r\n          </nz-form-label>\r\n          <nz-form-control>\r\n            <nz-switch [(ngModel)]=\"noResult\" (ngModelChange)=\"noResultChange($event)\" name=\"noResult\"></nz-switch>\r\n          </nz-form-control>\r\n        </nz-form-item>\r\n        <nz-form-item>\r\n          <nz-form-label>\r\n            <label>Simple Pagination</label>\r\n          </nz-form-label>\r\n          <nz-form-control>\r\n            <nz-switch [(ngModel)]=\"simple\" name=\"simple\"></nz-switch>\r\n          </nz-form-control>\r\n        </nz-form-item>\r\n        <nz-form-item>\r\n          <nz-form-label>\r\n            <label>Size</label>\r\n          </nz-form-label>\r\n          <nz-form-control>\r\n            <nz-radio-group [(ngModel)]=\"size\" name=\"size\">\r\n              <label nz-radio-button nzValue=\"default\">Default</label>\r\n              <label nz-radio-button nzValue=\"middle\">Middle</label>\r\n              <label nz-radio-button nzValue=\"small\">Small</label>\r\n            </nz-radio-group>\r\n          </nz-form-control>\r\n        </nz-form-item>\r\n      </form>\r\n    </div>\r\n    <nz-table\r\n      #dynamicTable\r\n      (nzCurrentPageDataChange)=\"currentPageDataChange($event)\"\r\n      [nzScroll]=\"fixHeader?{ y: '240px' }:null\"\r\n      [nzData]=\"dataSet\"\r\n      [nzBordered]=\"bordered\"\r\n      [nzSimple]=\"simple\"\r\n      [nzLoading]=\"loading\"\r\n      [nzFrontPagination]=\"pagination\"\r\n      [nzShowPagination]=\"pagination\"\r\n      [nzFooter]=\"footer?'Here is Footer':null\"\r\n      [nzTitle]=\"title?'Here is Title':null\"\r\n      [nzSize]=\"size\">\r\n      <thead>\r\n        <tr *ngIf=\"header\">\r\n          <th nzWidth=\"50px\" nzShowExpand *ngIf=\"expandable\"></th>\r\n          <th nzWidth=\"62px\" nzShowCheckbox *ngIf=\"checkbox\" [(nzChecked)]=\"allChecked\" [nzIndeterminate]=\"indeterminate\" (nzCheckedChange)=\"checkAll($event)\"></th>\r\n          <th nzWidth=\"150px\">Name</th>\r\n          <th nzWidth=\"70px\">Age</th>\r\n          <th>Address</th>\r\n          <th nzWidth=\"260px\">Action</th>\r\n        </tr>\r\n      </thead>\r\n      <tbody>\r\n        <ng-template ngFor let-data [ngForOf]=\"dynamicTable.data\">\r\n          <tr>\r\n            <td nzShowExpand *ngIf=\"expandable\" [(nzExpand)]=\"data.expand\"></td>\r\n            <td nzShowCheckbox *ngIf=\"checkbox\" [(nzChecked)]=\"data.checked\" (nzCheckedChange)=\"refreshStatus($event)\"></td>\r\n            <td>{{data.name}}</td>\r\n            <td>{{data.age}}</td>\r\n            <td>{{data.address}}</td>\r\n            <td>\r\n              <a href=\"#\">Action 一 {{data.name}}</a>\r\n              <nz-divider nzType=\"vertical\"></nz-divider>\r\n              <a href=\"#\">Delete</a>\r\n            </td>\r\n          </tr>\r\n          <tr [nzExpand]=\"data.expand&&expandable\">\r\n            <td></td>\r\n            <td [attr.colspan]=\"checkbox?5:4\">{{data.description}}</td>\r\n          </tr>\r\n        </ng-template>\r\n      </tbody>\r\n    </nz-table>`,\r\n  styles  : [\r\n      `\r\n      .components-table-demo-control-bar {\r\n        margin-bottom: 10px;\r\n      }\r\n\r\n      .components-table-demo-control-bar ::ng-deep .ant-form-item {\r\n        margin-right: 15px;\r\n        margin-bottom: 8px;\r\n      }\r\n    `\r\n  ]\r\n})\r\nexport class NzDemoTableDynamicSettingsComponent implements OnInit {\r\n  dataSet = [];\r\n  bordered = false;\r\n  loading = false;\r\n  pagination = true;\r\n  header = true;\r\n  title = true;\r\n  footer = true;\r\n  fixHeader = false;\r\n  size = 'small';\r\n  expandable = true;\r\n  checkbox = true;\r\n  allChecked = false;\r\n  indeterminate = false;\r\n  displayData = [];\r\n  simple = false;\r\n  noResult = false;\r\n\r\n  currentPageDataChange($event: Array<{ name: string; age: number; address: string; checked: boolean; expand: boolean; description: string; }>): void {\r\n    this.displayData = $event;\r\n    this.refreshStatus();\r\n  }\r\n\r\n  refreshStatus(): void {\r\n    const validData = this.displayData.filter(value => !value.disabled);\r\n    const allChecked = validData.length > 0 && validData.every(value => value.checked === true);\r\n    const allUnChecked = validData.every(value => !value.checked);\r\n    this.allChecked = allChecked;\r\n    this.indeterminate = (!allChecked) && (!allUnChecked);\r\n  }\r\n\r\n  checkAll(value: boolean): void {\r\n    this.displayData.forEach(data => {\r\n      if (!data.disabled) {\r\n        data.checked = value;\r\n      }\r\n    });\r\n    this.refreshStatus();\r\n  }\r\n\r\n  ngOnInit(): void {\r\n    for (let i = 1; i <= 20; i++) {\r\n      this.dataSet.push({\r\n        name       : 'John Brown',\r\n        age        : `${i}2`,\r\n        address    : `New York No. ${i} Lake Park`,\r\n        description: `My name is John Brown, I am ${i}2 years old, living in New York No. ${i} Lake Park.`,\r\n        checked    : false,\r\n        expand     : false\r\n      });\r\n    }\r\n  }\r\n\r\n  noResultChange(status: boolean): void {\r\n    this.dataSet = [];\r\n    if (!status) {\r\n      this.ngOnInit();\r\n    }\r\n  }\r\n}\r\n"