module.exports = "import { Component, OnInit } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-table-row-selection-and-operation',\r\n  template: `\r\n    <div style=\"margin-bottom: 16px;\">\r\n      <button nz-button [disabled]=\"disabledButton\" [nzType]=\"'primary'\" [nzLoading]=\"operating\" (click)=\"operateData()\">\r\n        Reload\r\n      </button>\r\n      <span style=\"margin-left: 8px;\" *ngIf=\"checkedNumber\">Selected {{checkedNumber}} items</span>\r\n    </div>\r\n    <nz-table\r\n      #rowSelectionTable\r\n      [nzData]=\"dataSet\"\r\n      (nzCurrentPageDataChange)=\"currentPageDataChange($event)\"\r\n      (nzPageIndexChange)=\"refreshStatus()\"\r\n      (nzPageSizeChange)=\"refreshStatus()\">\r\n      <thead>\r\n        <tr>\r\n          <th nzShowCheckbox [(nzChecked)]=\"allChecked\" [nzIndeterminate]=\"indeterminate\" (nzCheckedChange)=\"checkAll($event)\"></th>\r\n          <th>Name</th>\r\n          <th>Age</th>\r\n          <th>Address</th>\r\n        </tr>\r\n      </thead>\r\n      <tbody>\r\n        <tr *ngFor=\"let data of rowSelectionTable.data\">\r\n          <td nzShowCheckbox [(nzChecked)]=\"data.checked\" (nzCheckedChange)=\"refreshStatus($event)\"></td>\r\n          <td>{{data.name}}</td>\r\n          <td>{{data.age}}</td>\r\n          <td>{{data.address}}</td>\r\n        </tr>\r\n      </tbody>\r\n    </nz-table>`,\r\n  styles  : []\r\n})\r\nexport class NzDemoTableRowSelectionAndOperationComponent implements OnInit {\r\n  allChecked = false;\r\n  disabledButton = true;\r\n  checkedNumber = 0;\r\n  displayData: Array<{ name: string; age: number; address: string; checked: boolean }> = [];\r\n  operating = false;\r\n  dataSet = [];\r\n  indeterminate = false;\r\n\r\n  currentPageDataChange($event: Array<{ name: string; age: number; address: string; checked: boolean }>): void {\r\n    this.displayData = $event;\r\n  }\r\n\r\n  refreshStatus(): void {\r\n    const allChecked = this.displayData.every(value => value.checked === true);\r\n    const allUnChecked = this.displayData.every(value => !value.checked);\r\n    this.allChecked = allChecked;\r\n    this.indeterminate = (!allChecked) && (!allUnChecked);\r\n    this.disabledButton = !this.dataSet.some(value => value.checked);\r\n    this.checkedNumber = this.dataSet.filter(value => value.checked).length;\r\n  }\r\n\r\n  checkAll(value: boolean): void {\r\n    this.displayData.forEach(data => data.checked = value);\r\n    this.refreshStatus();\r\n  }\r\n\r\n  operateData(): void {\r\n    this.operating = true;\r\n    setTimeout(_ => {\r\n      this.dataSet.forEach(value => value.checked = false);\r\n      this.refreshStatus();\r\n      this.operating = false;\r\n    }, 1000);\r\n  }\r\n\r\n  ngOnInit(): void {\r\n    for (let i = 0; i < 46; i++) {\r\n      this.dataSet.push({\r\n        name   : `Edward King ${i}`,\r\n        age    : 32,\r\n        address: `London, Park Lane no. ${i}`,\r\n        checked: false\r\n      });\r\n    }\r\n  }\r\n}\r\n"