module.exports = "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-table-size',\r\n  template: `\r\n    <h4>Middle size table</h4>\r\n    <nz-table\r\n      #middleTable\r\n      nzSize=\"middle\"\r\n      [nzData]=\"data\">\r\n      <thead>\r\n        <tr>\r\n          <th>Name</th>\r\n          <th>Age</th>\r\n          <th>Address</th>\r\n        </tr>\r\n      </thead>\r\n      <tbody>\r\n        <tr *ngFor=\"let data of middleTable.data\">\r\n          <td>{{data.name}}</td>\r\n          <td>{{data.age}}</td>\r\n          <td>{{data.address}}</td>\r\n        </tr>\r\n      </tbody>\r\n    </nz-table>\r\n    <h4>Small size table</h4>\r\n    <nz-table\r\n      #smallTable\r\n      nzSize=\"small\"\r\n      [nzData]=\"data\">\r\n      <thead>\r\n        <tr>\r\n          <th>Name</th>\r\n          <th>Age</th>\r\n          <th>Address</th>\r\n        </tr>\r\n      </thead>\r\n      <tbody>\r\n        <tr *ngFor=\"let data of smallTable.data\">\r\n          <td>{{data.name}}</td>\r\n          <td>{{data.age}}</td>\r\n          <td>{{data.address}}</td>\r\n        </tr>\r\n      </tbody>\r\n    </nz-table>\r\n  `,\r\n  styles  : [\r\n    `h4 { margin-bottom: 16px; }`\r\n  ]\r\n})\r\nexport class NzDemoTableSizeComponent {\r\n  data = [\r\n    {\r\n      key    : '1',\r\n      name   : 'John Brown',\r\n      age    : 32,\r\n      address: 'New York No. 1 Lake Park',\r\n    },\r\n    {\r\n      key    : '2',\r\n      name   : 'Jim Green',\r\n      age    : 42,\r\n      address: 'London No. 1 Lake Park',\r\n    },\r\n    {\r\n      key    : '3',\r\n      name   : 'Joe Black',\r\n      age    : 32,\r\n      address: 'Sidney No. 1 Lake Park',\r\n    }\r\n  ];\r\n}\r\n"